import { FormEvent, useEffect, useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { supabase } from '../../lib/supabaseClient'
import { useAuthContext } from '../../context/AuthContext'

type Mode = 'login' | 'signup'

export default function AuthPage() {
  const [mode, setMode] = useState<Mode>('login')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [role, setRole] = useState<'user' | 'admin'>('user')
  const [loading, setLoading] = useState(false)
  const [message, setMessage] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  const navigate = useNavigate()
  const { session, profile, loading: authLoading, refreshProfile } = useAuthContext()

  useEffect(() => {
    if (authLoading) return
    if (session && profile?.role) {
      navigate(profile.role === 'admin' ? '/admin' : '/home', { replace: true })
    }
  }, [session, profile, authLoading, navigate])

  const title = useMemo(() => (mode === 'login' ? '' : ''), [mode])

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault()
    setLoading(true)
    setMessage(null)
    setError(null)

    try {
      if (!email || !password) {
        throw new Error('   .')
      }

      if (mode === 'login') {
        const { error: signInError } = await supabase.auth.signInWithPassword({
          email,
          password
        })
        if (signInError) throw signInError
        await refreshProfile()
        setMessage(' .')
      } else {
        const { data: signUpData, error: signUpError } = await supabase.auth.signUp({
          email,
          password,
          options: {
            data: { role }
          }
        })
        if (signUpError) throw signUpError
        if (signUpData.user) {
          const { error: profileError } = await supabase.from('user_info').insert({
            id: signUpData.user.id,
            email,
            name: signUpData.user.user_metadata?.name ?? null,
            role,
            status: role === 'admin' ? 'pending' : 'approved',
            department: null
          })
          if (profileError) throw profileError
        }
        setMessage(' .  .')
      }
    } catch (submitError) {
      const errorMessage = submitError instanceof Error ? submitError.message : '    .'
      setError(errorMessage)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-slate-50 flex items-center justify-center px-4">
      <div className="w-full max-w-md bg-white shadow-xl rounded-2xl p-8">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-2xl font-bold text-slate-900">{title}</h1>
          <div className="flex gap-2 text-sm">
            <button
              type="button"
              onClick={() => setMode('login')}
              className={`px-3 py-1 rounded-md ${
                mode === 'login' ? 'bg-blue-600 text-white' : 'bg-slate-100 text-slate-600'
              }`}
            >
              
            </button>
            <button
              type="button"
              onClick={() => setMode('signup')}
              className={`px-3 py-1 rounded-md ${
                mode === 'signup' ? 'bg-blue-600 text-white' : 'bg-slate-100 text-slate-600'
              }`}
            >
              
            </button>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label htmlFor="email" className="block text-sm font-medium text-slate-700">
              
            </label>
            <input
              id="email"
              type="email"
              value={email}
              onChange={(event) => setEmail(event.target.value)}
              className="mt-1 w-full rounded-lg border border-slate-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="example@company.com"
              required
            />
          </div>

          <div>
            <label htmlFor="password" className="block text-sm font-medium text-slate-700">
              
            </label>
            <input
              id="password"
              type="password"
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              className="mt-1 w-full rounded-lg border border-slate-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="8  "
              required
            />
          </div>

          {mode === 'signup' && (
            <div>
              <span className="block text-sm font-medium text-slate-700 mb-2"> </span>
              <div className="flex gap-3">
                <label className="flex-1 cursor-pointer rounded-lg border border-slate-200 px-3 py-2 text-center">
                  <input
                    type="radio"
                    name="role"
                    value="user"
                    checked={role === 'user'}
                    onChange={() => setRole('user')}
                    className="mr-2"
                  />
                  
                </label>
                <label className="flex-1 cursor-pointer rounded-lg border border-slate-200 px-3 py-2 text-center">
                  <input
                    type="radio"
                    name="role"
                    value="admin"
                    checked={role === 'admin'}
                    onChange={() => setRole('admin')}
                    className="mr-2"
                  />
                  
                </label>
              </div>
            </div>
          )}

          <button
            type="submit"
            disabled={loading}
            className="w-full rounded-lg bg-blue-600 py-3 text-white font-semibold hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {loading ? ' ...' : title}
          </button>
        </form>

        {message && <p className="mt-4 text-sm text-green-600">{message}</p>}
        {error && <p className="mt-4 text-sm text-red-600">{error}</p>}

        <p className="mt-6 text-xs text-slate-500">
          Supabase /  .       .
        </p>
      </div>
    </div>
  )
}


